.Resume{
    background-color: rgba(17, 15, 15, 0.7);
    border-radius: 5px;
    padding: 20px;
    text-align: left;
    margin:auto;
    margin-top: 100px;
    color:whitesmoke;
    width: min(90%,600px);
    justify-content:center;
}
.Resume ul{
color:whitesmoke;
}
.Resume ul a{
    color:blue;
}
.Resume li{
    padding: 5px;
    font-size: 16px;
    color:whitesmoke;
}
.listHeader{
    font-size: 20px;
    font-weight: bold;
}
@media (max-width:600px) {
    .Resume{
        width: 95%;
        padding: 10px;
        overflow: wrap;
    }
    .Resume ul{
    width: 95%;
    }
    .Resume li{
        width: 95%;
    }
}