.NavBar{
    font-size:22px;
   
    background-color: #000000;
    width: 100%;
    color:whitesmoke;
    box-shadow: 0 3px 4px black;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content:flex-end;
    position:relative;
}
.DarkModeIcon{
    font-size:20px;

}
.navbar-header{
    text-decoration: none;
    padding: 0.25em 1em;
    color:aliceblue;
    vertical-align: middle;
    position: absolute;
    left:0;
    font-size:1.5rem;
    font-weight:bold;
}
.small_nav-header{
    font-size:1.5rem;
    font-weight:bold;
    vertical-align: middle;
    margin: auto;
}

.NavBar ul{
    text-decoration: none;
    display: inline;
    margin:0;
    padding:0;
    display: flex;
    justify-content:flex-end;
    align-items:center;
    text-align:center;
}
.NavbarElement{
    text-decoration: none;
    padding: 0.2em 1.5em 0.2em 1.5em;
    display: inline-block;
    color:whitesmoke;
    transition: 0.15s;

    vertical-align: middle;
    margin-left: 20px;
    
}

.NavbarElement:hover,.NavbarElement:active{
    color:whitesmoke;
    transform: translateY(-5px);
    background-color: #ff005d;
   border-radius: 3px;


}
.NavBar button{
    border: none;
    color:whitesmoke;
    background-color: black;
    font-size: 25px;
}
.Active{
    color:whitesmoke;
    border-radius: 3px;
    background-color:#ff005d;
}
.header-f{
    color:white;
}
.header-l{
    color:#ff005d;
}
.btn-grp{
     position: absolute;
     left:5%;
}