.Home{
    padding:0;
    margin:0;
    height: 100%;
    width: 100vw;
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction: column;
}
html, body {
    height: 100%;
    width: 100%;
}

*{
    box-sizing: border-box;
}
body{
    padding:0;
    margin:0;
}
html{
    background:#D6C7A8;
    margin:0;
    padding:0;
    width:100vw;
}

.Main{
    color:whitesmoke;

margin: auto;
border-radius: 25px;


font-size: 25px;
}
.SText{
    font-size: 30px;
    margin-top: 200px;
    width: 50%;
    margin: auto;
    display:inline-block;
bottom:5%;
left:5%;
}
.About{
    color:whitesmoke;
    font-size: 25px;
    padding: 20px;
    border-radius: 25px;
    border:dashed rgb(15, 78, 180) 2px;
    width: 80%;
    margin: auto;
    margin-top: 100px;

}

.first-section{
    width:100vw;
    height:min(100vw,800px);
    background-image: url('./bg2.jpg');
    margin:0;

    display:flex;
    justify-content:center;
    align-items:flex-start;
    
}
.Skills{
    display: flex;
    justify-content:center;
    background-color: rgba(17, 15, 15, 0.7);
    border-radius: 5px;
    padding: 20px;
    margin: 50px;
    color:whitesmoke;
    margin-top: 100px;
    width: min(500px,80vw);
    display: inline-block;
    margin-bottom: 20px;
    opacity: 0.8;
    transition: 0.15s;
    box-sizing: border-box;
    position: relative;
    box-shadow:3px 3px 10px 3px #ff005d;
    overflow: hidden;
}
.badge{
    width:10rem;
    height :3rem;
    background-color: #ff005d;
    position: absolute;
    top:0;
    right:0;
    transform-origin:center;
    transform:rotateZ(45deg) translate(3rem,-3rem);
    overflow:hidden;
    text-align:center;
    font-size: 30px;
}
.Skills ul {
    text-decoration: none;
    text-align: center;
    font-size:20px;
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content:center;
}
.Skills li{
    font-weight : bold;
    background-color: rgb(250, 167, 197,0.5);
    border:#ff005d solid 3px;
    border-radius: 50%;
    height:150px;
    width:150px;
    display:flex;
    justify-content:center;
    align-items: center;
    margin:10px;
    overflow-wrap: break-word;
}
.Skills span{
    
}
.openMenu{
  width: 35px;
  height: 5px;
  background-color: #ff005d;
  margin: 6px 0;
  transition: .15s;
}
.menu-bar-icon :hover ,.menu-bar-icon:active{
    opacity : 0.8;
    background-color:whitesmoke;
    
}
.About{
    display: flex;
    justify-content:center;
    background-color: rgba(17, 15, 15, 0.7);
    box-shadow:3px 3px 10px 3px #ff005d;
    border-radius: 5px;
    padding: 20px;
    margin: 50px;
    color:whitesmoke;
    margin-top: 100px;
    width: min(500px,80vw);
    display: inline-block;
    margin-bottom: 20px;
    opacity: 0.8;
    transition: 0.15s;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
}
.About:hover{
    background-color:black;
}
.Skills:hover{
    background-color:black;
}
.About ul{
    text-decoration: none;
    text-align: center;
    font-size:20px;
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content:center;
}
.About li{
    font-weight: bold;
}
@media(max-width:600px)
{
 .SText{
    width: 100%;
    display: block;
 }   
 .first-section{
    flex-direction: column;
    justify-content:center;
    align-items:center;
 }
}