*{
    box-sizing: border-box
}
.SideBar{
    font-size:20px;
    height: 100vh;
    background-color: black;
    color:whitesmoke;
    width: fit-content;
    z-index: 1000;
    position:absolute;
    display:flex;
    left: 0;
    top:0;
    margin:0;
    opacity: 1;
    transition:transform,0.15s;

}

.close{
    transform:translateX(-40vw);
}
.open{
    transform:translateX(0);
}
.SideBar ul{
    text-decoration: none;
    margin: 0;
    width:40vw;
    display: flex;
    justify-content:center;
    align-items:middle;
    list-style: none;
    flex-direction: column;
    padding:0;
    text-align:center;
}
.SideBarElement{
    text-decoration: none;
    padding: 20px;
    width: 100%;
    display: flex;
    color:whitesmoke;
    transition: 0.15s;
    text-align:center;
    justify-content:center

  
}

.SideBarElement:hover,.SideBar:focus{
    color:white;
    background-color: #ff005d;


}
.Active1{
    color:white;
    background-color: #ff005d;
}